// previewMenuModal.js
export function initPreviewModal() {
  const modal = document.getElementById("preview-modal");
  const modalImg = modal.querySelector(".modal-imagem img");
  const modalTitulo = modal.querySelector(".modal-titulo");
  const modalDescricao = modal.querySelector(".modal-descricao");
  const modalPreco = modal.querySelector(".modal-preco");
  const btnClose = modal.querySelector(".modal-close");

  const items = document.querySelectorAll(".item-preview");

  items.forEach(item => {
    item.addEventListener("click", () => {
      const imgSrc = item.querySelector("img").src;
      const title = item.querySelector("h3").textContent;
      const desc = item.querySelector("p") ? item.querySelector("p").textContent : "";
      const preco = item.querySelector(".preco") ? item.querySelector(".preco").textContent : "";

      modalImg.src = imgSrc;
      modalImg.alt = title;
      modalTitulo.textContent = title;
      modalDescricao.textContent = desc;
      modalPreco.textContent = preco;

      modal.style.display = "flex";
    });
  });

  // Fechar modal
  btnClose.addEventListener("click", () => {
    modal.style.display = "none";
  });

  // Fechar clicando fora do conteúdo
  modal.addEventListener("click", e => {
    if (e.target === modal) modal.style.display = "none";
  });

  // Fechar com ESC
  document.addEventListener("keydown", e => {
    if (e.key === "Escape") modal.style.display = "none";
  });
}
