// previewMenu.js
export function initPreviewMenu() {
  const items = document.querySelectorAll(".menu-preview-item");

  items.forEach(item => {
    item.addEventListener("click", () => {
      alert(`Você clicou em: ${item.querySelector("h3").textContent}`);
      // Aqui você pode abrir um modal futuramente, ou direcionar para o item específico no cardápio
    });
  });
}
